import { mapState } from 'vuex'
export default {
	data() {
		return {
			isSelectPath:false
		}
	},
	onLoad(e) {
		if(e.type === "selectPath"){
			this.isSelectPath = true
		}
	},
	computed:{
		// 获取里面的数据
		...mapState({
			list:state => state.path.list
		})
	},
	methods: {
		// 跳转到新增的地址页面
		navtoNewPath(){
			uni.navigateTo({
				url:"../MyAddPath/MyAddPath"
			})
		},
		tonavPath(index){
			let pathObj = {
				index:index,
				item:this.list[index]
			}
			uni.navigateTo({
				url:`../MyAddPath/MyAddPath?data=${pathObj}`
			})
		},
		// 返回订单首页
		goConfirmOrder(item){
			
			// 如果是从确认订单过来的执行以下代码
			 if(this.isSelectPath){
				 // 自定义事件，页面通讯
				 uni.$emit('selectPathItem',item)
				 // 返回上一页
				 uni.navigateBack({
				 	delta:1
				 })
			 }
		}
	}
}
